@import "../../styles/lavenst.scss";

.post {
    &.header {
        & h2 {
            text-align: center;
            margin-top: 32px;

            @include mobile {
                font-size: 24px;
            }
        }

        & .time {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 16px;
            margin-bottom: 32px;

            & .icon {
                font-size: 16px;
                margin-right: 8px;
            }

            & span {
                font-size: 15px;
                font-weight: 500;
            }
        }
    }

    &.footer {
        display: flex;
        align-items: center;
        justify-content: center;

        & article.footer_control {
            margin-top: 32px;
            padding: 16px;
            border-top: 1px solid #e6e6e6;
            flex-direction: row;
            display: flex;

            & .btn {
                width: 32px;
                height: 32px;
                border: 1px solid #d4d4d4;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                transition: all .4s ease;
                margin: 0 8px;

                & .icon {
                    font-size: 12px;
                    color: #9e9e9e;
                    transition: all .4s ease;
                }

                &:hover {
                    &.fb {
                        background: $-fb-color;
                        border-color: $-fb-color;
                    }
                    & .icon {
                        color: #fff;
                    }
                }

                &.in:hover {
                    background: $-in-color;
                    border-color: $-in-color;
                }

                &.tw:hover {
                    background: $-tw-color;
                    border-color: $-tw-color;
                }
                
                &.pt:hover {
                    background: $-pt-color;
                    border-color: $-pt-color;
                }
            }
        }
    }
}