@import "../../styles/lavenst.scss";

nav {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999999;
    background: #fff;
    position: sticky;
    top: 0;

    & .section {
        display: flex;
        align-items: center;

        & .search_container {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            transition: all .4s ease;

            & .icon {
                font-size: 18px;
                cursor: pointer;
                margin-right: 18px;
                opacity: 0;
                animation: goToLeft .4s .2s forwards;

                &.close {
                    margin: 0 !important;
                    cursor: pointer;
                    color: #787878;
                }
            }

            & input {
                flex: 1;
                height: 100%;
                font-size: 14px;
                margin: 0 24px;
                color: #424242;
                opacity: 0;
                animation: goToLeft .4s .2s forwards;

                &::placeholder {
                    color: #787878;
                    font-size: 14px;
                }
            }
        }

        & .menu_btn {
            display: none;

            & .icon {
                font-size: 18px;
            }

            @include mobile {
                display: block;
            }
        }

        & .logo {
            margin-right: 24px;

            & img {
                height: 40px;
            }
            
            @include mobile {
                margin: auto;
            }
        }

        & .icon_container {
            display: flex;

            & .icon {
                font-size: 18px;
                cursor: pointer;
            }
        }

        & ul.nav {
            list-style-type: none;
            display: flex;
            padding: 0;
            flex: 1;
            justify-content: flex-end;
            align-items: center;
            opacity: 0;
            margin: 0;
            height: 100%;
            animation: .4s menuItemOpacity forwards;

            @include mobile {
                display: none;
            }

            & li {
                margin: 16px;
                height: 100%;
                display: flex;
                align-items: center;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    margin-right: 0;
                }

                > a {
                    height: 100%;
                    font-size: 12px;
                    font-weight: 600;
                    position: relative;
                    display: flex;
                    align-items: center;

                    &::before {
                        content: "";
                        width: 0;
                        height: 1px;
                        background: #414141;
                        position: absolute;
                        top: calc(50% + 14px);
                        left: 50%;
                        transform: translateX(-50%);
                        transition: 0.3s ease;
                    }
                }

                &:hover {
                    > a {
                        text-decoration: none;

                        &::before {
                            width: 100%;
                        }
                    }

                    & .sub {
                        visibility: visible;
                        opacity: 1;
                        top: 70px;
                    }
                }

                & .sub {
                    width: 100%;
                    position: absolute;
                    left: 0;
                    top: 58px;
                    transition: .5s ease;
                    padding: 32px 0;
                    visibility: hidden;
                    opacity: 0;
                    background: #fff;

                    & ul.section {
                        list-style-type: none;
                        padding: 0;
                        display: flex;
                        flex-wrap: wrap;

                        & li {
                            margin: 16px;

                            & a {
                                display: flex;
                                align-items: center;
                                flex-direction: column;

                                &::before {
                                    display: none;
                                }

                                & .image {
                                    width: 300px;
                                    height: 140px;
                                    background: #414141;
                                }

                                & span {
                                    display: block;
                                    margin-top: 13px;
                                    text-align: center;
                                    position: relative;

                                    &::before {
                                        content: "";
                                        width: 0;
                                        height: 1px;
                                        background: #414141;
                                        position: absolute;
                                        top: calc(100% + 5px);
                                        left: 50%;
                                        transform: translateX(-50%);
                                        transition: 0.3s ease;
                                    }
                                }
                            }

                            &:hover {
                                & a {
                                    & span {
                                        &::before {
                                            width: 100%;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.menu_drawer {
    top: 0;
    left: 0;
    z-index:99999999;
    width: 100vw;
    height: 100vh;
    background: #fff;
    padding: 20px 14px;
    box-sizing: border-box;
    position: fixed;
    
    & .closeBtn {
        font-size: 24px;
    }

    & ul {
        list-style-type: none;
        padding: 0;
        margin: 0;

        & li {
            margin: 32px 0;
            font-size: 18px;
            text-align: center;

            &:first-child {
                margin-top: 24px;
            }
        }
    }
}

.nav {
    &.child_section {
        width: 100%;
        height: 32px;
        display: flex;
        border-bottom: 1px  solid #eae8e4;
        align-items: center;
        justify-content: center;

        @include mobile {
            display: none;
        }

        & .container {
            width: 100%;
            max-width: 1080px;
            display: flex;
            align-items: center;
        }

        & .left, .right {
            flex: 1;
        }

        & .right {
            display: flex;
            justify-content: flex-end;
        }

        & span {
            font-size: 11px;
            font-weight: 600;
            text-decoration: none;
            color: #404852;
        }
    }
}