.section,section {
    width: $-section-width;
    margin: auto;

    @include mobile {
        width: calc(100% - 32px);
    }

    & .list {
        display: flex;
        flex-wrap: wrap;
        width: calc(#{$-section-width} + 24px);
        margin-left: -12px;
        margin-top: -24px;

        @include mobile {
            width: 100%;
            margin: 0;
            flex-direction: column;
        }

        & .column_l {
            flex: 1;
            margin: 12px;

            @include mobile {
                margin: 12px 0;
            }
        }
    }

    & .row {
        display: flex;
        flex-wrap: wrap;

        @include mobile {
            flex-direction: column;
        }
    }

    & .column {
        display: flex;
        flex-direction: column;
    }

    & .col {
        flex: 1;
    }

    & .col-8 {
        max-width: 66.666667%;
        flex: 0 0 66.666667%;
    }
}