@import "../../styles/lavenst.scss";

.carousel {
    width: 100%;
    height: 100vh;
    background: #414141;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    & h1 {
        font-size: 48px;
        color: #fff;
        font-weight: bold;
        text-shadow: 0 2px 14px rgba(0,0,0,.9);
        text-align: center;

        @include mobile {
            font-size: 32px;
        }
    }
    
    & .image {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-attachment: fixed;
        background-position: center bottom;
        background-repeat: no-repeat;
        background-size: cover;
    }

    & .overlay {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        background: rgba(0,0,0,.15);
    }

    &.sect {
        height: 380px;
    }
}