@import "../../styles/lavenst.scss";

.breadcrumb {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding: 0;
    margin-bottom: 32px;
    margin-top: 64px;
    list-style: none;
    background-color: #fff;
    border-radius: .25rem;
    display: flex; 
    align-items: center;

    & li {
        display: flex;
        align-items: center;
    }

    & a {
        text-decoration: none;
        background-color: transparent;
        -webkit-text-decoration-skip: objects;
        font-size: 12px;
        text-transform: uppercase;
        cursor: pointer;
        font-weight: 500;
        margin-right: 12px;
        color: #888;

        &:hover {
            text-decoration: underline;
        }
    }

    & .icon {
        margin-right: 12px;
        color: #888;

        &.home {
            font-size: 16px;
        }
    }

    & .breadcrumb-item:first-child::before {
        display: none;
    }
}