@import "../../styles/lavenst.scss";

.section_c  {
    &.product {
        flex: 1;
        margin: 0 12px;
        height: 200px;
        border: 1px solid $-gray-0;
        cursor: pointer;
        position: relative;
        text-decoration: none;

        @include mobile {
            margin: 12px 0;
            flex: 1;
            padding-bottom: 100px;

            &:first-child {
                margin: 12px 0!important;
            }
        }

        &:hover {
            & a {
                bottom: 24px;
                opacity: 1;
            }
        }

        & h3 {
            margin: 24px 24px 0 20px;
        }

        & span {
            display: block;
            margin: 4px 24px;
            font-size: 12px;
        }

        & a {
            display: block;
            position: absolute;
            bottom: 0;
            opacity: 0;
            left: 24px;
            font-size: 15px;
            font-weight: 600;
            transition: .5s ease;
            text-transform: uppercase;

            &:hover {
                text-decoration: none;
            }
        }

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &.header {
        margin-top: 64px;
        margin-bottom: 32px;
        position: relative;
        width: 100%;

        @include mobile {
            margin-top: 32px;
            margin-bottom: 12px;
        }

        &.control {
            & .row{
                align-items: center;

                & span {
                    margin-right: 16px;
                }
            }
        }

        &.column {
            & h4 {
                font-size: 16px;
            }

            @include mobile {
                margin-bottom: 24px;
            }
        }

        & span {
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
        }

        & h4 {
            margin-top: 4px;
            text-transform: uppercase;
        }

        & a {
            position: absolute;
            right: 0;
            bottom: 0;
            font-weight: 600;
            font-size: 14px;
            opacity: .75;

            @include mobile {
                display: none;
            }
        }
    }

    &.help {
        display: inline-block;
        & .help_item {
            display: flex;
            align-items: center;
            margin-top: 16px;

            &:first-child {
                margin-top: 0;
            }

            & .icon {
                margin-right: 8px;
            }

            & span {
                display: block;
            }
        }
    }
}