h1, h2, h3, h4, h5, h6, a, span, p {
    margin: 0;
    color: $-dark;

    &.text-center {
        text-align: center;
    }

    &.text-right {
        text-align: right;
    }

    &.text-left {
        text-align: left;
    }
}

p {
    line-height: 1.8;
}

.icon {
    color: $-dark;
    font-size: 20px;
}

h1 {
    font-size: 40px;
}

h2 {
    font-size: 32px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 20px;
}

h6 {
    font-size: 16px;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

.badge {
    display: inline-block;
    padding: .25em .8em;
    font-size: 75%;
    font-weight: 600;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: .25rem;
    color: #fff;
    background-color: $-green;
}