@import "../../styles/lavenst.scss";

.product_summary {
    width: 100%;
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    & .product_image_container {
        display: flex;
        flex-direction: column;

        & .image {
            background-size: cover;
            background-position: top center;
            background-repeat: no-repeat;
            background-color: #414141;
            width:475px;
            position: relative;

            @include mobile {
                width: 100%;
            }

            & .share_container {
                width: 40px;
                position: absolute;
                background: #fff;
                top: 0px;
                left: 0px;

                & .control {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;

                    & .icon {
                        color: #b6b6b6;
                        font-size: 14px;
                        transition: all .4s ease;
                    }

                    &:hover {
                        & .icon {
                            color: $-green;
                        }
                    }

                    &::before {
                        content: "";
                        display: block;
                        position: relative;
                        padding-top: 100%;
                    }
                }
            }
        }

        & .image_items {
            display: flex;
            background-size: cover;
            background-position: top center;
            background-repeat: no-repeat;
            cursor: pointer;

            & .item {
                flex: 1;
                margin: 12px;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    margin-right: 0;
                }

                & .item_image {
                    width: 100%;
                    background-color: #fff;
                    opacity: .5;

                    &::before {
                        content: "";
                        display: block;
                        position: relative;
                        padding-top: 100%;
                    }
                }

                &.active {
                    & .item_image{
                        opacity: 1;
                    }
                }
            }
        }
    }

    & .summary_container {
        flex: 1;
        padding-left: 32px;
        display:flex;
        flex-direction: column;
        max-height: 475px;

        @include mobile {
            max-height: unset;
            padding-left: 0;
        }

        & .summary {
            flex: 1;
        }

        & hr {
            border: none;
            width: 64px;
            height: 1px;
            background: #000;
            opacity: .15;
            float:left;
            margin: 0;
            padding: 0;
            margin: 12px 0;

            &.full {
                width: 100%;
                margin-top: 24px;
            }
        }

        & .summary_control {
        }

        & h2 {
            @include mobile {
                font-size: 28px;
            }
        }

        & h4 {
            display: block;
            width: 100%;
            font-weight: 600;
        }

        & p {
            margin-top: 24px;
            text-align:justify;
            font-weight: 500;
            color: rgb(78, 78, 78);
            font-size: 15px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 3; /* number of lines to show */
            -webkit-box-orient: vertical;
        }

        & span.tags {
            margin-top: 24px;
            display: block;
            font-size: 12px;
            font-weight: 500;

            & b {
                color: $-green;
            }
        }

        & button {
            margin-bottom: 32px;

            @include mobile {
                margin-right: 0;
            }

            &:first-child {
                margin-left :0;
            }
        }
    }
}

.product_tabs {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 32px;

    & .tabs_container {
        width: 100%;
        padding-bottom: 12px;
        border-bottom: 1px solid rgba(136, 136, 136, 0.25);
        display: flex;

        @include mobile {
            margin-top: 32px;
        }

        & .tab {
            height: 100%;
            margin:0 12px;
            position: relative;
            cursor: pointer;

            &.active {
                & span {
                    color: #000;
                }

                &::before {
                    content: "";
                    display: block;
                    position: relative;
                    width: 100%;
                    height: 2px;
                    background: #000;
                    bottom: -12px;
                    position: absolute;
                }
            }

            &:first-child {
                margin-left: 0;
            }

            & span {
                font-size: 14px;
                font-weight: 700;
                color: #888;

                @include mobile {
                    font-size: 11px;
                }
            }
        }
    }

    & .tab_content {
        flex: 1;
        padding: 24px 0 0 0;

        & p {
            font-size: 15px;
            font-weight: 500;
            line-height: 1.8;
            text-align:justify;
        }
    }
}