button {
    background: transparent;
    padding: 0;
    height: 40px;
    margin: 16px;
    border: none;
    outline: none;
    border-radius: 0;
    padding: 0 32px;
    color: #fff;
    font-size: 13px;
    background-color: $-green;
    font-family: $-default-font;
    transition: all .2s ease-out;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;

    & .icon {
        color: #fff;
        margin-right: 8px;
    }

    &:hover {
        background: $-green-6;
    }

    &.outline {
        background: transparent;
        border: 1px solid  $-green;
        color:  $-green;

        & .icon {
            color: $-green;
        }
        
        &:hover {
            background:  $-green;
            color: #fff;

            & .icon {
                color: #fff;
            }
        }
    }

    &.link {
        background: transparent;
        color: $-green;

        &:hover {
            background: $-green-0;
            color: $-green;
        }
    }

    &.lg {
        height: 56px;
        padding: 0 48px;
        font-size: 15px;
    }

    &.sm {
        height: 32px;
        font-size: 11px;
    }

    &.block {
        width: 100%;
    }

    &:disabled {
        opacity: .5;
        cursor: default;

        &.outline {
            &:hover {
                background: transparent;
                color: $-green;
            }
        }

        &.link {
            &:hover {
                background: transparent;
                color: $-green;
            }
        }
    }
}