@import "../../styles/lavenst.scss";

.card {
    &.gallery {
        flex: 0 0 calc(33.333333% - 24px);
        height: 450px;
        margin: 24px 12px;
        cursor: pointer;

        @include mobile {
            margin: 12px 0;
        }

        & .image_container {
            width: 100%;
            height: 400px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;

            & a {
                display: block;
                position: absolute;
                margin-top: 24px;
                opacity: 0;
                color: #fff;
                font-size: 15px;
                font-weight: 600;
                transition: .5s ease;
                text-transform: uppercase;
                z-index: 2;
            }

            & .image {
                width: 100%;
                height: 100%;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: top center;
                transition: all .5s ease;
            }

            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                background: rgba(0, 0, 0, .4);
                opacity: 0;
                transition: all .5s ease;
            }
        }

        &:hover {

            & .image_container::after {
                opacity: 1;
            }

            & .image_container {
                & a {
                    opacity: 1;
                    margin-top: 0;
                }

                & .image {
                    transform: scale(1.05);
                }
            }

            & .information_container {
                & .info {
                    & a {
                        color: $-green;
                    }
                }
            }
        }

        & .information_container {
            width: 100%;
            display: flex;
            flex-direction: row;

            & .info {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                flex: 1;

                & a {
                    font-weight: 500;
                    margin: 14px 0;
                    font-weight: bold;
                    transition: all .4s ease;
                }

                & span {
                    font-weight: 500;
                    font-size: 14px;
                    color: $-dark-2;
                }
            }
        }
    }

    &.product {
        flex: 0 0 calc(33.333333% - 24px);
        height: 450px;
        margin: 24px 12px;

        &.second {
            flex: 1;
            display: flex;
            height: auto;
            margin-left: 0;

            & .image_container {
                height: 75px;
                width: 75px;

                & .image {
                    &::after {
                        display: none;
                    }
                }
            }

            & .information_container {
                flex: 1;
                display: flex;
                flex-direction: row;

                & .info {
                    margin-left: 12px;

                    & a {
                        margin-top: 0;
                        font-size: 15px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 1; /* number of lines to show */
                        -webkit-box-orient: vertical;
                    }

                    & span {
                        font-size: 14px;
                    }
                }

                & .control {
                    flex: unset;
                    width: 48px;
                }
            }
        }

        @include mobile {
            margin: 12px 0;
        }

        &:hover {
            text-decoration: none;

            & .image_container::after {
                opacity: 1;
            }

            & .image_container {
                & a {
                    opacity: 1;
                    margin-top: 0;
                }

                & .image {
                    transform: scale(1.05);
                }
            }

            & .information_container {
                & .control {
                    & .icon {
                        margin-right: 12px;
                        opacity: 1;
                    }
                }
            }
        }

        & .image_container {
            width: 100%;
            height: 400px;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;

            & a {
                display: block;
                position: absolute;
                margin-top: 24px;
                opacity: 0;
                color: #fff;
                font-size: 15px;
                font-weight: 600;
                transition: .5s ease;
                text-transform: uppercase;
                z-index: 2;
            }

            & .image {
                width: 100%;
                height: 100%;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: top center;
                transition: all .5s ease;
            }

            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                background: rgba(0, 0, 0, .4);
                opacity: 0;
                transition: all .5s ease;
            }
        }

        & .information_container {
            width: 100%;
            display: flex;
            flex-direction: row;

            & .info {
                display: flex;
                flex-direction: column;
                flex: 1;

                & a {
                    font-weight: 500;
                    margin: 14px 0;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1; /* number of lines to show */
                    -webkit-box-orient: vertical;
                }

                & span {
                    font-weight: 500;
                    font-size: 14px;
                    color: $-dark-2;
                }
            }

            & .control {
                flex: 1;
                display: flex;
                align-items: center;
                justify-content: flex-end;

                & .icon {
                    margin-right: 24px;
                    opacity: 0;
                    transition: all .4s ease;
                }
            }
        }
    }

    &.news {
        flex: 1;

        @include mobile {
            height: 183px;
            display: inherit;
        }

        &.second {
            margin: 48px 12px;
            display: block;

            @include mobile {
                margin: 12px 0;
                height: 183px;
                display: inherit;

                &:first-child {
                    margin-top: 24px;
                }

                & .container {
                    & .image_container {
                        & .image {
                            height: 100%;
                        }
                    }

                    & .info {
                        & .container {
                            & h5 {
                                font-size: 14px;
                            }
                        }
                    }
                }
            }

            & .container {
                & .info {
                    & .container {
                        & h5 {
                            color: #fff;
                        }
                    }
                }
            }

            &:last-child {
                margin-right: 0;
            }
        }

        &.column {
            margin: 24px 12px;
            display: block;
            width: 340px;
            flex: unset;

            @include mobile {
                margin: 24px 0;
                width: auto;
                flex: 1;
                height: auto;
            }

            &:hover {
                & .container {
                    & .info {
                        & .container {
                            & h5 {
                                color: $-green;
                            }
                        }

                        & .control {
                            & span {
                                margin-left: 0;
                                opacity: 1;
                            }

                            & .icon {
                                margin-left: 0;
                                margin-right: 0;
                            }
                        }
                    }
                }
            }

            & .container {

                & .image_container::after {
                    display: none;
                }

                & .info {
                    position: relative;
                    flex-direction: column;

                    & .container {
                        padding: 24px 0;

                        & h5 {
                            transition: all .5s ease;
                        }

                        & p {
                            font-size: 14px;
                            margin-top: 24px;
                            text-align: justify;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 4;
                            color: #888;
                            /* number of lines to show */
                            -webkit-box-orient: vertical;
                        }
                    }

                    & .control {
                        justify-content: flex-end;

                        & span {
                            font-size: 14px;
                            font-weight: bold;
                            margin-right: 8px;
                            opacity: 0;
                            margin-left: -18px;
                            transition: .4s ease;
                            color: $-green;
                        }

                        & .icon {
                            color: $-green;
                            font-size: 18px;
                            margin-left: -18px;
                        }
                    }
                }
            }
        }

        &:hover {
            & .container {
                & .image_container {
                    & .image {
                        transform: scale(1.05);
                    }

                    &::after {
                        opacity: 1;
                    }
                }

                & .info {
                    & .control {
                        & .icon {
                            margin-right: 36px;
                            opacity: 1;
                        }
                    }
                }
            }
        }

        & .container {
            width: 100%;
            position: relative;

            @include mobile {
                height: 100%;
            }

            & .image_container {
                width: 100%;
                height: 100%;
                overflow: hidden;

                & .image {
                    width: 100%;
                    background-position: top center;
                    background-size: cover;
                    background-repeat: no-repeat;
                    transition: all .5s ease;

                    @include mobile {
                        height: 100%;
                    }

                    &:after {
                        content: "";
                        display: block;
                        padding-bottom: 50%;
                    }
                }

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    background: rgba(0, 0, 0, .4);
                    opacity: 0;
                    transition: all .5s ease;
                }
            }

            & .info {
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 4;
                display: flex;

                & .container {
                    flex: 1;
                    padding: 36px;
                    position: relative;

                    & h3 {
                        color: #fff;

                        @include mobile {
                            font-size: 14px;
                        }
                    }

                    & span {
                        display: block;
                        margin-top: 12px;
                        font-size: 14px;
                        color: #fff;

                        @include mobile {
                            font-size: 12px;
                        }
                    }
                }

                & .control {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    & .icon {
                        margin-right: 48px;
                        color: #fff;
                        font-size: 20px;
                        transition: .4s ease;
                        opacity: 0;
                    }
                }
            }
        }

        &:hover {
            text-decoration: none;
        }
    }
}